body {
  --main-color: #fff;
  --rank-color: #8b0000;
  --name-color: #2f4f4f;
  margin: 0;
  font-family: Arial, sans-serif;
}

.photo {
  width: 200px;
  height: 200px;
  margin-bottom: 8px;
  position: relative;
}

.photo img {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  transition: border-radius 1s;
  display: block;
}

.photo .rank {
  background-color: var(--main-color);
  color: var(--rank-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-family: Arial;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  bottom: 50px;
  right: 20px;
}

.person {
  background-color: var(--main-color);
  transform-origin: center;
  transition: transform 1s;
}

.person:hover {
  transform: scale(1.2);
}

.person .details {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.person .details .name {
  color: var(--name-color);
  margin-bottom: 10.8px;
  font-family: Arial;
  font-size: 20px;
  font-weight: 700;
}

.person .details .extra {
  text-align: center;
  opacity: 0;
  color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  font-size: 21.6px;
  font-weight: 400;
  display: flex;
}

.person:hover .details .extra {
  opacity: 1;
}

.person:hover .photo img {
  border-radius: 60px;
}

.people {
  padding: 30px 305px 157px;
}

.people .wrapper {
  justify-content: center;
  place-items: center;
  gap: 30px 40px;
  display: grid;
}

@media (width >= 500px) {
  .people .wrapper {
    grid-template-columns: repeat(2, 1fr);
    padding: 30px 35px 185px;
  }
}

@media (width >= 750px) {
  .people .wrapper {
    grid-template-columns: repeat(2, 1fr);
    padding: 30px 160px 155px;
  }
}

@media (width >= 1000px) {
  .people .wrapper {
    grid-template-columns: repeat(4, 1fr);
    padding-top: 30px;
    padding-left: 65px;
    padding-right: 65px;
  }
}

@media (width >= 1200px) {
  .people .wrapper {
    grid-template-columns: repeat(4, 1fr);
    padding-left: 88px;
    padding-right: 88px;
  }
}
